@import '../includes';


body {
  position: relative;
}

.nav_container {
  padding: 0 3rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;

  @media (max-width: "900px") {
    padding: 0 1rem;
  }
}


.navbar {

  &>.container-fluid {
    @media (max-width: "900px") {
      justify-content: flex-start;
      padding: 0;
    }

  }

  .menu_btn {
    i {
      cursor: pointer;
      font-size: 2rem;
      color: white
    }
  }

  &-brand {
    margin-left: 2rem;

    @media (max-width: 900px) {
      margin-left: 5px;
    }

    img {
      width: 160px;

      @media (max-width: 900px) {
        width: 120px;
      }
    }

  }

  #navbarSupportedContent {
    display: flex;
    margin-left: auto;
  }


  .search {
    height: 50px;
    position: relative;
    margin-right: 1rem;

    &__toggle:checked {
      ~.search__field {
        width: 400px;
        border-width: 2px;
        background-color: #fff;

        @media (max-width: 767px) {
          position: fixed;
          top: 21%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 80%;
        }

        .search__input {
          &::placeholder {
            opacity: 1;
          }
        }

        .search__close {
          left: -25px;
          display: block;
          font-size: 1.8rem;
        }

        .search__button {
          right: 5px;
          width: 35px;
          height: 35px;
          border-radius: 50px;
          background: transparent;

          &--toggle {
            display: none;
          }

          &--submit {
            display: flex;
          }
        }
      }
    }

    &__close {
      position: relative;
      top: 50%;
      left: 0;
      transform: translateX(-50%);
      transition: .3s;
      display: none;

      i {
        color: var(--blue);
        ;
      }


    }

    &__field {
      position: absolute;
      right: 0;
      left: 50%;
      width: 40px;
      height: 45px;
      border: 0px solid #fff;
      background: transparent;
      border-radius: 50px;
      transform: translateX(-110%);
      transform-origin: left;
      transition: .3s;
    }

    &__input {
      position: absolute;
      left: 0;
      box-sizing: border-box;
      width: calc(100% - 40px);
      height: 100%;
      padding: 5px 15px;
      color: #777777;
      background-color: transparent;
      border: 0;
      outline: none;
      font-family: 'Roboto', sans-serif;
      font-size: 16px;

      &::placeholder {
        color: #777777;
        opacity: 0;
        transition: .3s;
        transition-delay: .3s;
      }
    }

    &__label {
      cursor: pointer;
    }

    &__button {
      position: absolute;
      top: 50%;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      border-radius: 5px;
      transform: translateY(-50%);
      cursor: pointer;
      transition: .3s;

      &--submit {
        display: none;
        border: 0;

        i {
          color: black;
          font-size: 1.6rem;
        }
      }
    }

    &__icon {
      position: relative;
      color: white;

      i {
        font-size: 1.8rem;
      }


    }
  }

}


.header {
  width: 100%;
  height: 100vh;




  .swiper {
    width: 100%;
    height: 100%;

    &-slide {
      position: relative;

      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        opacity: 0.85;
        z-index: 1;
        background: linear-gradient(to left top, rgba(0, 0, 0, 0) 0px, rgba(0, 0, 0, 0.1) 15%, rgba(0, 0, 0, 0.45) 40%, rgba(0, 0, 0, 0.8) 75%, rgba(0, 0, 0, 0.9) 80%, rgba(0, 0, 0, 0.95) 100%) !important;
      }

      &:after {
        content: '';
        background: linear-gradient(to bottom left, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.1) 15%, rgba(0, 0, 0, 0.45) 40%, rgba(0, 0, 0, 0.8) 75%, rgba(0, 0, 0, 0.95) 80%, #000 100%) !important;
        position: absolute;
        top: 0;
        left: 0;
        opacity: .75;
        width: 100%;
        height: 100%;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }


      &_desc {
        position: absolute;
        top: 35%;
        z-index: 99;
        left: 4rem;
        width: 50%;

        @media (max-width: 767px) {
          top: 30%;
          left: 1rem;
          width: 94%;
        }

        h2 {
          font-size: 2.75rem;
        }

        &-text {
          margin: 1.75rem 0;
          color: #C5C5C5;
          max-width: 600px;
          font-weight: 400;

          @media only screen and (max-width: 991.98px) {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
        }

        button i {
          margin-right: 5px;
        }
      }

      .blue_line {
        height: 0.25rem;
        width: 6.875rem;
        background-color: var(--blue);
        margin: 1rem 0 .7rem;
      }

      .rate {
        color: var(--blue);
        font-weight: bold;
      }

    }
  }
}

.swiper-horizontal>.swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: 3rem;
  width: 100%;
  left: 33%;
  @media (max-width: 767px) {
    bottom: 1.5rem;
  }
  .swiper-pagination-bullet {
    box-sizing: content-box;
    flex: 0 1 auto;
    width: 0.75rem;
    height: 0.75rem;
    margin-right: 4px;
    margin-left: 4px;
    text-indent: -999px;
    cursor: pointer;
    background-color: #fff;
    background-clip: padding-box;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    opacity: .5;
    transition: opacity 0.6s ease;
    height: 0.75rem;
    border: none;

    &.swiper-pagination-bullet-active {
      width: 1.25rem;
      background-color: #9E1E62;
      border-radius: 1rem;
      opacity: 1;
    }
  }
}

.video-container {
  
  width: 800px;
  border-radius: 4px;
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 8rem;

  @media (max-width: 900px) {
    width: 100%;
    margin-top: 10rem;
  }

  &+ button {
    width: 800px;
    @media (max-width: 900px) {
      width: 100%;
    }
  }
}

video {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}

.video-controls {
  right: 0;
  left: 0;
  padding: 10px;
  position: absolute;
  bottom: 0;
  transition: all 0.2s ease;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.5));
}

.video-controls.hide {
  opacity: 0;
  pointer-events: none;
}

.video-progress {
  position: relative;
  height: 8.4px;
  margin-bottom: 10px;
}

progress {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 2px;
  width: 100%;
  height: 8.4px;
  pointer-events: none;
  position: absolute;
  top: 0;
}

progress::-webkit-progress-bar {
  background-color: #474545;
  border-radius: 2px;
}

progress::-webkit-progress-value {
  background: var(--youtube-red);
  border-radius: 2px;
}

progress::-moz-progress-bar {
  border: 1px solid var(--youtube-red);
  background: var(--youtube-red);
}

.seek {
  position: absolute;
  top: 0;
  width: 100%;
  cursor: pointer;
  margin: 0;
}

.seek:hover+.seek-tooltip {
  display: block;
}

.seek-tooltip {
  display: none;
  position: absolute;
  top: -50px;
  margin-left: -20px;
  font-size: 12px;
  padding: 3px;
  content: attr(data-title);
  font-weight: bold;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.6);
}

.bottom-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left-controls {
  display: flex;
  align-items: center;
  color: #fff;
}

.volume-controls {
  display: flex;
  align-items: center;
  margin-right: 10px;
}

.volume-controls input {
  width: 100px;
  opacity: 1;
  transition: all 0.4s ease;
}

.volume-controls:hover input,
.volume-controls input:focus {
  width: 100px;
  opacity: 1;
}

button {
  cursor: pointer;
  position: relative;
  margin-right: 7px;
  font-size: 12px;
  padding: 3px;
  border: none;
  outline: none;
  background-color: transparent;
}

button * {
  pointer-events: none;
}

button::before {
  content: attr(data-title);
  position: absolute;
  display: none;
  right: 0;
  top: -50px;
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  font-weight: bold;
  padding: 4px 6px;
  word-break: keep-all;
  white-space: pre;
}

button:hover::before {
  display: inline-block;
}

.fullscreen-button {
  margin-right: 0;
}

.pip-button svg {
  width: 26px;
  height: 26px;
}

.playback-animation {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -40px;
  margin-top: -40px;
  width: 80px;
  height: 80px;
  border-radius: 80px;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
}

input[type=range] {
  -webkit-appearance: none;
  -moz-appearance: none;
  height: 8.4px;
  background: transparent;
  cursor: pointer;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  cursor: pointer;
  border-radius: 1.3px;
  -webkit-appearance: none;
  transition: all 0.4s ease;
}

input[type=range]::-webkit-slider-thumb {
  height: 16px;
  width: 16px;
  border-radius: 16px;
  background: var(--youtube-red);
  cursor: pointer;
  -webkit-appearance: none;
  margin-left: -1px;
}

input[type=range]:focus::-webkit-slider-runnable-track {
  background: transparent;
}

input[type=range].volume {
  height: 5px;
  background-color: #fff;
}

input[type=range].volume::-webkit-slider-runnable-track {
  background-color: transparent;
}

input[type=range].volume::-webkit-slider-thumb {
  margin-left: 0;
  height: 14px;
  width: 14px;
  background: #fff;
}

input[type=range]::-moz-range-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  border: 1px solid transparent;
  background: transparent;
  border-radius: 1.3px;
}

input[type=range]::-moz-range-thumb {
  height: 14px;
  width: 14px;
  border-radius: 50px;
  border: 1px solid var(--youtube-red);
  background: var(--youtube-red);
  cursor: pointer;
  margin-top: 5px;
}

input[type=range]:focus::-moz-range-track {
  outline: none;
}

input[type=range].volume::-moz-range-thumb {
  border: 1px solid #fff;
  background: #fff;
}

.hidden {
  display: none;
}

svg {
  width: 28px;
  height: 28px;
  fill: #fff;
  stroke: #fff;
  cursor: pointer;
}


